import React, { useEffect, useRef, useState } from "react";
import s from "./Card.module.css";

type PropsType = {
    value: string;
    countActiveElements: number;
    selectedItems: { id: number; value: string; color: string }[];
    isCorrect: boolean;
    setIsCorrect: (isCorrect: boolean) => void;
    setCountActiveElements: (e: number) => void;
    setSelectedItems: (
        e: { id: number; value: string; color: string }[]
    ) => void;
    socket: any;
    isFlipped: boolean;
    id: number;
    currentUser: string;
    isFirstAction: boolean | null;
    isGuessed: boolean;
    firstUser: string;
    secondUser: string;
    color: string;
    prevGuessedCard: number[];
    isAnimEnded: boolean;
};

export const Card: React.FC<PropsType> = React.memo(
    ({
        isGuessed,
        isFirstAction,
        currentUser,
        id,
        isFlipped,
        socket,
        value,
        countActiveElements,
        selectedItems,
        isCorrect,
        setIsCorrect,
        firstUser,
        secondUser,
        color,
        prevGuessedCard,
        isAnimEnded,
    }) => {
        const ref = useRef<HTMLDivElement>(null);

        const cardBody = (
            <>
                <div className={s.front__side}>
                    <img
                        src={`https://i.pinimg.com/originals/38/52/18/385218163958b5817ded6373dd9f01a6.png`}
                        alt=""
                    />
                </div>
                <div
                    className={`${s.back__side} ${
                        color == "Red" ? s.redSide : s.blackSide
                    }`}
                >
                    <p>{isFlipped ? value : ""}</p>
                </div>
            </>
        );
        useEffect(() => {
            if (countActiveElements === 0 && isCorrect) setIsCorrect(false);
        }, [countActiveElements]);
        return (
            <div
                className={`${s.card} ${
                    isFlipped ? `${s.active__card} ${s.flipped__card}` : ""
                } ${isGuessed ? `hiddenEl${id}` : ""}`}
                ref={ref}
                onClick={() => {
                    if (selectedItems.length == 2) {
                        return;
                    }
                    if (isFlipped) {
                        return;
                    }
                    if (currentUser === firstUser && isFirstAction) {
                        socket.current.send(
                            JSON.stringify({
                                action: "was_flipped",
                                order: id,
                                username: currentUser,
                            })
                        );
                    } else if (!isFirstAction && currentUser === secondUser) {
                        socket.current.send(
                            JSON.stringify({
                                action: "was_flipped",
                                order: id,
                                username: currentUser,
                            })
                        );
                    }
                }}
            >
                {prevGuessedCard.includes(id) && isAnimEnded
                    ? ""
                    : prevGuessedCard.includes(id) && !isAnimEnded
                    ? cardBody
                    : isGuessed
                    ? ""
                    : cardBody}
            </div>
        );
    }
);
