const SHUFFLE_CARD = "SHUFFLE_CARD";
const CURRENT_USER = "CURRENT_USER";
const SET_USER_PHOTO = "SET_USER_PHOTO";
const IS_FIRST_ACTION = "IS_FIRST_ACTION";
const CHANGE_CARD = "CHANGE_CARD";
const GUESSED_CARD = "GUESSED_CARD";
const SET_WIN = "SET_WIN";
const SET_SCORE = "SET_SCORE";
const PREV_GUESSED_CARDS = "PREV_GUESSED_CARDS";

let initialState = {
    cardsOrder: [] as [] | CardType[],
    win: false,
    currentUser: "",
    ownPhoto: "",
    userPhoto: "",
    firstUser: "",
    secondUser: "",
    isFirstAction: true,
    firstScore: 0,
    secondScore: 0,
    guessedCards: 0,
    prevGuessedCards: [] as [] | number[],
    isAnimEnded: false,
};
export type CardType = {
    rank: string;
    color: string;
    flipped: boolean;
    guessed: boolean;
    order: number;
};
export type InitialStateType = typeof initialState;

export const cardReducer = (
    state = initialState,
    action: any
): InitialStateType => {
    switch (action.type) {
        case SHUFFLE_CARD: {
            return {
                ...state,
                cardsOrder: action.cards,
            };
        }

        case CURRENT_USER: {
            return {
                ...state,
                firstUser: action.firstUser,
                secondUser: action.secondUser,
                currentUser: action.currUser,
            };
        }
        case SET_USER_PHOTO: {
            return {
                ...state,
                ownPhoto: action.ownPhoto,
                userPhoto: action.userPhoto,
            };
        }
        case SET_SCORE: {
            return {
                ...state,
                firstScore: action.firstScore,
                secondScore: action.secondScore,
            };
        }
        case IS_FIRST_ACTION: {
            return {
                ...state,
                isFirstAction: action.isFirstAction,
            };
        }

        case CHANGE_CARD: {
            return {
                ...state,
                cardsOrder: state.cardsOrder.map((e) => {
                    if (action.id === e.order) {
                        return { ...e, flipped: !e.flipped };
                    } else {
                        return e;
                    }
                }),
            };
        }
        case GUESSED_CARD: {
            return {
                ...state,
                cardsOrder: state.cardsOrder.map((e) => {
                    if (
                        action.value.card1.order !== e.order &&
                        action.value.card2.order !== e.order
                    ) {
                        return e;
                    } else {
                        return { ...e, guessed: !e.guessed };
                    }
                }),
            };
        }
        case PREV_GUESSED_CARDS: {
            return {
                ...state,
                isAnimEnded: action.value.length == 0 ? true : false,
                prevGuessedCards: action.value,
            };
        }
        case SET_WIN: {
            return {
                ...state,
                win: true,
            };
        }
        default: {
            return state;
        }
    }
};

export const setShuffleCard = (cards: string[]) => {
    return {
        type: SHUFFLE_CARD,
        cards,
    };
};

export const setCurrentUser = (
    firstUser: string,
    secondUser: string,
    currUser: string
) => {
    return {
        type: CURRENT_USER,
        firstUser,
        secondUser,
        currUser,
    };
};

export const setUserPhoto = (ownPhoto: string, userPhoto: string) => {
    return {
        type: SET_USER_PHOTO,
        ownPhoto,
        userPhoto,
    };
};

export const setIsFirstAction = (isFirstAction: boolean) => {
    return {
        type: IS_FIRST_ACTION,
        isFirstAction,
    };
};

export const changeCard = (id: number) => {
    return {
        type: CHANGE_CARD,
        id,
    };
};

export const guessedCard = (value: {
    card1: { order: number };
    card2: { order: number };
    score_first_user: number;
    score_second_user: number;
}) => {
    return {
        type: GUESSED_CARD,
        value,
    };
};

export const setScore = (firstScore: number, secondScore: number) => {
    return {
        type: SET_SCORE,
        firstScore,
        secondScore,
    };
};

export const setWin = () => {
    return {
        type: SET_WIN,
    };
};

export const setPrevGuessedCards = (value: number[]) => {
    return {
        type: PREV_GUESSED_CARDS,
        value,
    };
};
