import React from "react";
import { useEffect, useState, useRef } from "react";
import s from "./Cards.module.css";
import sn from "./Card/Card.module.css";
import { Card } from "./Card/Card";
import { useSelector, useDispatch } from "react-redux";
import { StateType } from "../../BLL/redux";
import sl from "./Card/Card.module.css";
import {
    changeCard,
    guessedCard,
    setCurrentUser,
    setUserPhoto,
    setIsFirstAction,
    setShuffleCard,
    setWin,
    setScore,
    CardType,
    setPrevGuessedCards,
} from "../../BLL/card-reducer";
import gsap from "gsap";

type ItemType = { id: number; value: string; color: string };

export const Cards: React.FC = React.memo(() => {
    const shuffledCardsArr = useSelector(
        (state: StateType) => state.cardReducer.cardsOrder
    );
    const ownPhoto = useSelector(
        (state: StateType) => state.cardReducer.ownPhoto
    );
    const userPhoto = useSelector(
        (state: StateType) => state.cardReducer.userPhoto
    );
    const currentUser = useSelector(
        (state: StateType) => state.cardReducer.currentUser
    );
    const firstUser = useSelector(
        (state: StateType) => state.cardReducer.firstUser
    );
    const secondUser = useSelector(
        (state: StateType) => state.cardReducer.secondUser
    );
    const guessedCards = useSelector(
        (state: StateType) => state.cardReducer.guessedCards
    );
    const isFirstAction = useSelector(
        (state: StateType) => state.cardReducer.isFirstAction
    );
    const firstScore = useSelector(
        (state: StateType) => state.cardReducer.firstScore
    );
    const secondScore = useSelector(
        (state: StateType) => state.cardReducer.secondScore
    );
    const isAnimEnded = useSelector(
        (state: StateType) => state.cardReducer.isAnimEnded
    );
    const prevGuessedCard = useSelector(
        (state: StateType) => state.cardReducer.prevGuessedCards
    );
    const usedCards = useRef<HTMLDivElement>(null);
    let [countActiveElements, setCountActiveElements] = useState(0);
    const [selectedItems, setSelectedItems] = useState<ItemType[]>([]);
    const [isCorrect, setIsCorrect] = useState(false);
    const dispatch = useDispatch();
    const wichUser = (param1: string | number, param2: string | number) => {
        if (currentUser == firstUser) {
            return param1;
        } else {
            return param2;
        }
    };
    const [isBasketEmpty, setBasket] = useState(false);

    const socketRef = useRef<WebSocket | null>(null);
    useEffect(() => {
        if (prevGuessedCard.length > 1) {
            prevGuessedCard.forEach((el: number) => {
                const card = document.querySelector(
                    `.hiddenEl${el}`
                ) as HTMLDivElement;

                if (card) {
                    const tl = gsap.timeline({
                        onComplete: () => {
                            if (!isBasketEmpty) {
                                setBasket(true);
                            }
                            dispatch(setPrevGuessedCards([]));
                        },
                    });

                    gsap.to(card, {
                        y: -50,
                        duration: 0.5,
                    });
                    tl.to(card, {
                        x:
                            //@ts-ignore
                            usedCards.current.getBoundingClientRect().x -
                            card.getBoundingClientRect().x,
                        y:
                            //@ts-ignore
                            usedCards.current.getBoundingClientRect().y -
                            card.getBoundingClientRect().y,
                        duration: 1,
                        delay: 0.5,
                        onComplete: () => {
                            card.classList.remove(`guessed__item`);
                            card.classList.add(sl.hiddenEl);
                        },
                    })
                        .to(
                            `.hiddenEl${el} .Card_front__side__X\\+xK3`,
                            {
                                rotateY: "0deg",
                                duration: 0.3,
                            },
                            "-=1.1"
                        )
                        .to(
                            `.hiddenEl${el} .${sn.back__side}`,
                            {
                                rotateY: "-180deg",
                                duration: 0.3,
                            },
                            "-=1.1"
                        );
                }
            });
        }
    }, [prevGuessedCard]);

    useEffect(() => {
        const currentURL = window.location.href;
        const urlParts = currentURL.split("/");
        const user1 = urlParts[urlParts.length - 4];
        const user2 = urlParts[urlParts.length - 3];
        socketRef.current = new WebSocket(
            `ws://127.0.0.1:8000/ws/${user1}/${user2}/`
        );

        socketRef.current.onclose = function (e) {
            console.error("Chat socket closed unexpectedly", e);
        };
        return () => {
            if (socketRef.current) {
                socketRef.current.close();
            }
        };
    }, []);
    useEffect(() => {
        //@ts-ignore
        socketRef.current.onmessage = function (e) {
            const parsedData = JSON.parse(e.data);
            if (shuffledCardsArr.length === 0) {
                const { first_user, second_user, curr_user } = parsedData;
                const photoUser =
                    curr_user == first_user
                        ? parsedData.first_avatar_url
                        : parsedData.second_avatar_url;
                const photoUserAn =
                    curr_user == first_user
                        ? parsedData.second_avatar_url
                        : parsedData.first_avatar_url;
                dispatch(setShuffleCard(parsedData.cards));
                dispatch(setCurrentUser(first_user, second_user, curr_user));
                dispatch(setUserPhoto(photoUser, photoUserAn));
                dispatch(setIsFirstAction(parsedData.is_turn_first_user));
                dispatch(
                    setScore(
                        parsedData.score_first_user,
                        parsedData.score_second_user
                    )
                );
            }
            if (parsedData.action === "change_turn") {
                dispatch(setIsFirstAction(parsedData.is_turn_first_user));
            }
            if (parsedData.action === "was_flipped") {
                dispatch(changeCard(parsedData.updated_card[4].order));
                if (parsedData.updated_card[2].flipped) {
                    setSelectedItems((prev) => [
                        ...prev,
                        {
                            id: parsedData.updated_card[4].order,
                            value: parsedData.updated_card[0].rank,
                            color: parsedData.updated_card[1].color,
                        },
                    ]);
                }
            }
            if (parsedData.action === "was_guessed") {
                let [cards] = parsedData.updated_cards_guessed;
                dispatch(guessedCard(cards));
                dispatch(
                    setPrevGuessedCards([cards.card1.order, cards.card2.order])
                );
                dispatch(
                    setScore(cards.score_first_user, cards.score_second_user)
                );
            }
        };
    }, [shuffledCardsArr]);

    useEffect(() => {
        if (selectedItems.length === 2) {
            const isEqual = selectedItems[0].value === selectedItems[1].value;
            const isColorEqual =
                selectedItems[0].color === selectedItems[1].color;
            if (!isColorEqual || !isEqual) {
                setTimeout(() => {
                    if (currentUser === firstUser && isFirstAction) {
                        selectedItems.forEach((e) => {
                            if (socketRef.current)
                                socketRef.current.send(
                                    JSON.stringify({
                                        action: "was_flipped",
                                        order: e.id,
                                        username: currentUser,
                                    })
                                );
                        });
                        socketRef.current?.send(
                            JSON.stringify({ action: "change_turn" })
                        );
                    } else if (!isFirstAction && currentUser === secondUser) {
                        selectedItems.forEach((e) => {
                            if (socketRef.current)
                                socketRef.current.send(
                                    JSON.stringify({
                                        action: "was_flipped",
                                        order: e.id,
                                        username: currentUser,
                                    })
                                );
                        });

                        socketRef.current?.send(
                            JSON.stringify({ action: "change_turn" })
                        );
                    }
                }, 500);
            }
            if (isEqual && isColorEqual) {
                setTimeout(() => {
                    if (currentUser === firstUser && isFirstAction) {
                        socketRef.current?.send(
                            JSON.stringify({
                                action: "was_guessed",
                                card1_order: selectedItems[0].id,
                                card2_order: selectedItems[1].id,
                                user: firstUser,
                            })
                        );
                    } else if (!isFirstAction && currentUser === secondUser) {
                        socketRef.current?.send(
                            JSON.stringify({
                                action: "was_guessed",
                                card1_order: selectedItems[0].id,
                                card2_order: selectedItems[1].id,
                                user: secondUser,
                            })
                        );
                    }
                }, 1000);
            }
            setSelectedItems([]);
        }
    }, [selectedItems]);
    useEffect(() => {
        if (guessedCards == 8) {
            if (currentUser === firstUser && isFirstAction) {
                //@ts-ignore
                socketRef.current.send(
                    JSON.stringify({
                        action: "end_game",
                        winner:
                            firstScore > secondScore ? firstUser : secondUser,
                        loser:
                            firstScore < secondScore ? secondUser : firstUser,
                    })
                );
            } else if (!isFirstAction && currentUser === secondUser) {
                //@ts-ignore
                socketRef.current.send(
                    JSON.stringify({
                        action: "end_game",
                        winner:
                            firstScore > secondScore ? firstUser : secondUser,
                        loser:
                            firstScore < secondScore ? secondUser : firstUser,
                    })
                );
            }

            dispatch(setWin());
            setBasket(false);
        }
    }, [guessedCards]);
    useEffect(() => {
        gsap.fromTo(
            `.${s.userPhoto}`,
            { opacity: 0, scale: 0, transformOrigin: "center" },
            { opacity: 1, scale: 1, duration: 0.6 }
        );
        gsap.fromTo(
            `.${s.userName}`,
            { opacity: 0, y: 50 },
            { opacity: 1, y: 0, duration: 0.6 }
        );
        gsap.fromTo(
            `.firstScore`,
            {
                x: -100,
                opacity: 0,
            },
            {
                x: 0,
                opacity: 1,
                duration: 0.4,
                ease: "power2.out",
            }
        );
        gsap.fromTo(
            `.secondScore`,
            {
                x: 100,
                opacity: 0,
            },
            {
                x: 0,
                opacity: 1,
                duration: 0.4,
                ease: "power2.out",
            }
        );
    }, []);
    return (
        <>
            <div className={s.wrapper}>
                <div className={`${s.users__info}`}>
                    <div className={s.user}>
                        <div className={s.mainUserInfo}>
                            <div className={s.userPhoto}>
                                <img src={ownPhoto} alt="" />
                            </div>
                            <h2 className={s.userName}>
                                {wichUser(firstUser, secondUser)}
                            </h2>
                        </div>
                        <h2 className={`${s.score} firstScore`}>
                            :{" "}
                            <span>{wichUser(firstScore, secondScore)}</span>
                        </h2>
                    </div>
                    <h2>VS</h2>
                    <div className={s.user}>
                        <div className={s.mainUserInfo}>
                            <div className={s.userPhoto}>
                                <img src={userPhoto} alt="" />
                            </div>
                            <h2 className={s.userName}>
                                {wichUser(secondUser, firstUser)}
                            </h2>
                        </div>
                        <h2 className={`${s.score} secondScore`}>
                            :{" "}
                            <span>{wichUser(secondScore, firstScore)}</span>
                        </h2>
                    </div>
                </div>
                <div className={`${s.container}`}>
                    {+firstScore + +secondScore !== 8 ? (
                        <>
                            <div className={s.cards}>
                                {shuffledCardsArr.map((e, i) => {
                                    return (
                                        <Card
                                            color={e.color}
                                            value={e.rank}
                                            key={i}
                                            countActiveElements={
                                                countActiveElements
                                            }
                                            currentUser={currentUser}
                                            isFirstAction={isFirstAction}
                                            setCountActiveElements={
                                                setCountActiveElements
                                            }
                                            isFlipped={e.flipped}
                                            selectedItems={selectedItems}
                                            setSelectedItems={setSelectedItems}
                                            isCorrect={isCorrect}
                                            setIsCorrect={setIsCorrect}
                                            socket={socketRef}
                                            isGuessed={e.guessed}
                                            id={e.order}
                                            firstUser={firstUser}
                                            secondUser={secondUser}
                                            prevGuessedCard={prevGuessedCard}
                                            isAnimEnded={isAnimEnded}
                                        />
                                    );
                                })}
                            </div>
                            <div className={s.usedCards} ref={usedCards}>
                                {isBasketEmpty || guessedCards > 0 ? (
                                    <img src="https://i.pinimg.com/originals/38/52/18/385218163958b5817ded6373dd9f01a6.png"></img>
                                ) : (
                                    ""
                                )}
                            </div>
                        </>
                    ) : (
                        <div className={s.win}>
                            <h1> !!!!!</h1>
                            <h2>
                                :{" "}
                                {firstScore > secondScore
                                    ? firstUser
                                    : secondUser}
                            </h2>
                            <button
                                onClick={() => {
                                    window.location.href =
                                        "http://127.0.0.1:8000/";
                                }}
                            >
                                
                            </button>
                        </div>
                    )}
                </div>
                {+firstScore + +secondScore !== 8 ? (
                    <div className={s.info}>
                        <p>: {isFirstAction ? firstUser : secondUser}</p>
                    </div>
                ) : (
                    ""
                )}
            </div>
        </>
    );
});
